.topbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  margin-bottom: 270px;

  @include onTablet {
    margin-bottom: 306px;
  }

  @include onDesktop {
    margin-bottom: 300px;
  }

  &__logo {
    @include hover(scale, 1.1);

    &-img {
      @include onTablet {
        width: 187px;
        height: 56px;
      }
    }
  }

  &__buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 24px;

    @include onTablet {
      gap: 32px;
    }
  }
}
