.contacts {
  padding-bottom: 120px;
  gap: 48px;

  @include onTablet {
    padding-bottom: 120px;
  }

  @include onDesktop {
    padding-bottom: 164px;
  }

  @include grid;

  &__heading {
    @include mobileHeading2;
    display: flex;
    justify-content: center;
    color: $color-text-main;


    grid-column: 1 / -1;

    @include onTablet {
      @include tabletHeading2;
    }

    @include onDesktop {
      @include desktopHeading2;
    }
  }

  &__form {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: 1 / 6;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 24px;

    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: 9 / -1;
    }

    &-wrap {
      display: flex;
      flex-direction: column;
      gap: 8px;
    }
  }

  &__title {
    @include contactsTitle;
  }

  &__link {
    @include hover(scale, 1.1);
    @include contactsLink;
    width: fit-content;
    text-decoration: none;
  }
}
