.header {
  position: relative;
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  height: 100vh;


  background: url(../images/Cover_photo.png) right/cover no-repeat;
  background-size: cover;
  background-position: 85% 55%;

  &__title {
    @include mobileHeading1;
    position: absolute;
    align-self: center;
    bottom: 88px;
    white-space: nowrap;
    color: $color-text-main;


    @include onTablet {
      @include tabletHeading1;
        bottom: 130px;
    }

    @include onDesktop {
      @include desktopHeading1;
        bottom: 203px;
    }
  }
}
