.details {

  &__heading {
    @include mobileHeading2;
    display: flex;
    justify-content: center;
    color: $color-text-main;

    margin-bottom: 48px;

    @include onTablet {
      @include tabletHeading2;
    }

    @include onDesktop {
      @include desktopHeading2;
    }
  }

  &__images {
    margin-bottom: 16px;

   &__wrapper {
    @include grid;

    &__img1-square {
      align-self: center;
      object-fit: cover;
      width: 100%;
      height: 100%;
      height: 100%;

      grid-column: 1 / 2;

      @include onTablet {
        display: none;
      }
    }

    &__img1-wide {
      display: none;
      align-self: center;
      object-fit: cover;
      width: 100%;
      height: 100%;

      @include onTablet {
        grid-column: span 4;
        display: block;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }

    &__img2 {
      align-self: center;
      object-fit: cover;
      width: 100%;
      height: 100%;

      grid-column: 2 / -1;

      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }

    &__img1-last {
      align-self: center;
      object-fit: cover;
      width: 100%;
      height: 100%;

      grid-column: 1 / 2;

      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }

    &__img2-square {
      align-self: center;
      object-fit: cover;
      width: 100%;
      height: 100%;

      grid-column: 2 / -1;

      @include onTablet {
        display: none;
      }
    }

    &__img2-wide {
      display: none;
      align-self: center;
      object-fit: cover;
      width: 100%;
      height: 100%;

      @include onTablet {
        display: block;

        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }

    }
   }
  }

  &__img-heading {
    margin-bottom: 8px;

    @include mobileCardHeading;
  }

  &__img-text {
    @include mobileCardText;

    margin-bottom: 40px;

    @include onTablet {
      margin-bottom: 48px;
    }

    @include onDesktop {
      max-width: 65%;
    }
  }

  &__button {
    margin: auto;
    width: 100%;

    @include onTablet {
      width: 392px;
    }

    @include onDesktop {
      width: 324px;
    }
  }
}
